a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

.memes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  margin-right: 20px;
  float: right;
}

#caption1,
#caption2 {
  position: relative;
}
#memeSelected {
  position: absolute;
}

body {
  background-color: gainsboro;
}

